import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom'
import styled from 'styled-components'
import './GoodDetail.scss'

let CTBOX1 = styled.div`
    padding: 12px;
`;

let CTTITLE = styled.div`
    color: ${props => props.txtColors};
`;



function GoodDetail(props) {
    let history = useHistory();
    let { id } = useParams();
    let [ViewFlag, ViewFlagUpdate] = useState(true);

    let [inputData, inputDataUpdate] = useState('');

    useEffect(() => { //컴포넌트 mount 되었을때, update 될때 실행코드

        //axios.get()

        //console.log(11111)
        //let 타이머 = setTimeout(() => { document.getElementById("my-alertbox").hidden = true; }, 2000)
        let 타이머 = setTimeout(() => { ViewFlagUpdate(false) }, 2000)
        return () => { clearTimeout(타이머) }
        //return function 어쩌구() { 실행할코드 } //사라질때 실행코드
    }, []); //[] ==>> 최초 조회시에만 

    // useEffect(() => { //컴포넌트 mount 되었을때, update 될때 실행코드
    //     //console.log(11111) 
    //     //let 타이머 = setTimeout(() => { document.getElementById("my-alertbox").hidden = true; }, 2000)
    //     return function 어쩌구() { 실행할코드 } //사라질때 실행코드
    // });


    // useEffect(() => { //컴포넌트 mount 되었을때, update 될때 실행코드
    //     //console.log(11111)
    //     //let 타이머 = setTimeout(() => { document.getElementById("my-alertbox").hidden = true; }, 2000)
    //     return function 어쩌구() { 실행할코드 } //사라질때 실행코드
    // });



    return (
        <div>
            <div className="container">
                <CTBOX1>
                    <CTTITLE txtColors={'orange'} >
                        Detail
                    </CTTITLE>
                    <CTTITLE txtColors="orange" >
                        Detail
                    </CTTITLE>
                    <CTTITLE className="red">제목입니다.</CTTITLE>
                </CTBOX1>
                {inputData}
                <input onChange={(e) => { inputDataUpdate(e.target.value) }} />

                {
                    ViewFlag === true
                        ? <UrgentBox></UrgentBox>
                        : null
                }

                <div className="row">
                    <div className="col-md-3">
                        <img src={'https://codingapple1.github.io/shop/shoes' + (props.GoodInfos[id].id + 1) + '.jpg'} width="100%" />
                    </div>
                    <div className="col-md-6 mt-4">
                        <h4 className="pt-5">{props.GoodInfos[props.GoodInfos[id].id].title}</h4>
                        <p>{props.GoodInfos[props.GoodInfos[id].id].content}</p>
                        <p>{props.GoodInfos[props.GoodInfos[id].id].price}원</p>


                        <StockInfo 재고={props.재고[props.GoodInfos[id].id]}></StockInfo>

                        <button className="btn btn-danger" onClick={() => {

                        }}>주문하기</button>
                        &nbsp;
                        <button className="btn btn-danger" onClick={() => { history.goBack(); }}>뒤로가기</button>
                        &nbsp;
                        <button className="btn btn-danger" onClick={() => { history.push('/'); }}>홈으로</button>
                    </div>
                </div>
            </div>
        </div >
    )
}


function StockInfo(props) {
    return (
        <p>재고: {props.재고}</p>
    )
}


function UrgentBox() {
    return (
        <div id="my-alertbox" className="my-alert2">
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>
    )
}


export default GoodDetail;